import { useState, useCallback, useEffect } from "react";
import { Box, Text, DataTable, Button } from "grommet";
import { Upload } from "grommet-icons";
import Card from "./components/Card";
import { useDropzone } from "react-dropzone";
import { Document, Page, pdfjs } from "react-pdf";

pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

const Docs = () => {
  const [numPages, setNumPages] = useState(null);
  const [pdf, setPdf] = useState(null);
  const [file, setFile] = useState(null);
  const [error, setError] = useState(null);
  const [reject, setReject] = useState(null);
  const [select, onSelect] = useState(null);

  const onDocumentLoadSuccess = ({ numPages }) => {
    setNumPages(numPages);
    setError(null);
  };

  useEffect(() => {
    if (reject) console.log("! only upload PDF");
  }, [reject]);

  const onDrop = useCallback((acceptedFiles, fileRejections) => {
    setFile(acceptedFiles);
    setReject(fileRejections.length > 0 ? true : false);
  }, []);

  const { getRootProps, getInputProps } = useDropzone({
    onDrop,
    accept: ".pdf",
  });

  const columns = [
    {
      property: "name",
      primary: true,
      header: <Text>File</Text>,
      render: (datum) => datum.name.replace(/\.[^.]+$/, ""),
    },
    {
      property: "size",
      header: <Text>kb</Text>,
      render: (datum) => Math.round(datum.size / 1024),
    },
    {
      property: "lastModified",
      header: <Text>Ver</Text>,
      render: (datum) =>
        new Date(datum.lastModified).toLocaleDateString("en-GB"),
    },
  ];

  return (
    <>
      <Box
        focusIndicator={false}
        pad={{ top: "xlarge", horizontal: "large" }}
        justify="center"
        align="center"
        {...getRootProps()}
      >
        {file ? (
          <DataTable
            columns={columns}
            data={file}
            step={10}
            select={select}
            onSelect={onSelect}
            onClickRow={(event) => setPdf(event.datum)}
          />
        ) : (
          <>
            <input {...getInputProps()} />
            <Card
              title="Upload file"
              subTitle="Click or drag'n'drop here"
              children={<Upload size="large" />}
              footer=""
              size="large"
            />
          </>
        )}
      </Box>
      <Box pad={{ vertical: "medium", horizontal: "19em" }}>
        {select && select.length !== 0 ? (
          <Button
            icon={<Upload />}
            label={select.length + " upload(s)"}
            size="small"
            reverse
            onClick={() => console.log(select)}
          />
        ) : null}
      </Box>
      {pdf ? (
        <Box pad="small" align="center">
          <Card
            title=""
            subTitle={!error ? <Text>{numPages} pages</Text> : null}
            children={
              <Document
                file={pdf}
                onLoadSuccess={onDocumentLoadSuccess}
                onLoadError={setError}
              >
                <Page pageNumber={numPages ? 1 : null} />
              </Document>
            }
            footer=""
            size="large"
          />
        </Box>
      ) : (
        <Box pad="large">
          {file ? (
            <Card
              title="Click on row"
              subTitle="to preview and upload"
              children={null}
              footer=""
              size="large"
            />
          ) : null}
        </Box>
      )}
    </>
  );
};

export default Docs;
